@import url(https://use.fontawesome.com/releases/v5.0.13/css/all.css);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
  font-family: Roboto;
  font-weight: 400;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;

}

.wrapper {
  background-color: #CCC;
  height: 100%;
  min-height: 100vh;
}

.container {
  width: 100%;
  max-width: 1250px;
  margin: 0 auto;
}

.title-app {
  text-align: center;
  padding: 50px 0;
  color: rgb(42, 27, 61);
  font-weight: bold;
}

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: 16px;
  color: rgb(49, 49, 49)
}

.modal {
  width: 100vw;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: center;
          justify-content: center;
  opacity: 0;
  padding-top: 30px;
  pointer-events: none;
  transition: opacity 0.3s;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal__content {
  padding: 25px;
  border-radius: 12px;
  background-color: rgb(189, 189, 189);
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  transition: all 0.3s;
  min-width: 320px;
}

.modal__content.active {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.id {
  display: none;
}

.table {
  width: 100%;
  font-size: 18px;
  border-collapse: collapse;
  border: 0;
  color: rgb(241, 240, 240);
}

.table__title {
  padding: 40px 0;
  text-align: center;
  font-weight: bold;
}

.table__title-row {
  width: 100%;
  background-color: #000;
}

.table__td {
  padding: 15px;
  text-align: center;
  border-spacing: 0;
}

.evenRow,
.btn_evenRow {
  background-color: #666;
}

.oddRow,
.btn_oddRow {
  background-color: #333;
}

.btn-add {
  background-color: #000;
}

.btn {
  border: none;
  padding: 0;
  outline: none;
  color: rgb(243, 240, 240);
  padding: 5px;
  cursor: pointer;
  transition: color 0.4s;
  font-size: 25px;
}

.btn-del:hover {
  color: rgb(184, 133, 133);
}

.btn-edit:hover {
  color: rgb(183, 195, 114);
}

.btn-add:hover {
  color: rgb(66, 175, 130);
}

.input {
  font-size: 16px;
  border: none;
  padding: 2px 20px 0;
  outline: none;
  border-bottom: #000 solid 3px;
  background-color: rgb(189, 189, 189);
  color: rgb(49, 49, 49);
  cursor: text;
}

.input::-webkit-input-placeholder {
  color: rgb(125, 125, 125);
}

.input:-ms-input-placeholder {
  color: rgb(125, 125, 125);
}

.input::placeholder {
  color: rgb(125, 125, 125);
}

.form-title {
  padding-bottom: 20px;
  max-width: 280px;
}

.radio {
  -webkit-transform: scale(1.6);
          transform: scale(1.6);
  opacity: 0.9;
  cursor: pointer;
}

.radio-box {
  display: -webkit-flex;
  display: flex;
  text-align: center;
  width: 100%;
  padding: 0 30px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  box-sizing: border-box;
  height: 23px;
}

.invalid {
  border-bottom: rgb(156, 46, 46) solid 3px;
  background-color: rgb(200, 189, 189);
}

.label-input_marg {
  margin-top: 12px;
}

.btn-form {
  background-color: rgb(86, 128, 74);
  border-radius: 20px;
  color: rgb(219, 219, 219);
  padding: 7px;
  margin-top: 15px;
  transition: background-color 0.4s;
}

.btn-form:hover {
  background-color: rgb(95, 149, 79);
  ;
}

.table__td-name_min {
  display: none
}
.cardWorker,cardWorker{
  position: relative;
}
.modalClose{
  position: absolute;
  top:0;
  right: 10px;
  font-size: 30px;
  cursor: pointer;
  transition: 0.4s;
}
.modalClose:hover {
  color: rgb(177, 111, 111);
}
@media screen and (max-width: 1024px) {
  .table__td {
    padding: 5px;
  }

  .table {
    font-size: 12px;
  }

  .btn {
    font-size: 17px;
  }

  .btn-form {
    padding: 15px;
  }

}

@media screen and (max-width: 620px) {
  .table__td_media {
    display: none;
  }

  .table__td-name_min {
    display: block;
    max-width: 90px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .table__td-name {
    display: none
  }

  .btn {
    padding: 2px;
  }

  .modal {
    -webkit-align-items: center;
            align-items: center;
    padding-top: 0;
  }

  .btn-form {
    padding: 15px;
  }
  .title-app {
    padding: 20px 0;
}
}

@media screen and (max-width: 419px) {
  .table__td_media-mob {
    display: none;
  }

  .table {
    font-size: 13px;
  }

  .table__td-name_min {
    max-width: 120px;
  }

  .modal {
    -webkit-align-items: flex-start;
            align-items: flex-start;
    box-sizing: border-box;
  }

  .btn-form {
    padding: 15px;
  }
}
@media screen and (max-height: 600px){
  .modal__content.active {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    top: -70px;
    position: absolute;
}
}
